<template>
<div>
  <h1>메인 화면</h1>
  <div id="app">
    <h1>서버 트래픽 모니터링</h1>
    <div v-if="loading">데이터를 불러오는 중...</div>
    <div v-else>
      <p>1분 단위 트래픽: {{ oneMinuteTraffic }} GB</p>
      <p>1시간 단위 트래픽: {{ oneHourTraffic }} GB</p>
      <p>24시간 단위 트래픽: {{ twentyFourHoursTraffic }} GB</p>
    </div>
  </div>

</div>

</template>


<script>

export default {
  name: 'DefaultLayout',
    components: {
  },
  data() {
    return {
      loading: true,
      oneMinuteTraffic: null,
      oneHourTraffic: null,
      twentyFourHoursTraffic: null
    };
  },
  created() {
    // 서버에서 트래픽 데이터를 가져오는 비동기 함수 호출
    this.fetchTrafficData();
  },
  methods: {
    async fetchTrafficData() {
      try {
        // 서버로부터 트래픽 데이터 가져오기
        const response = await fetch('/api/traffic');
        const data = await response.json();
        // 가져온 데이터를 변수에 할당
        this.oneMinuteTraffic = data.oneMinuteTraffic;
        this.oneHourTraffic = data.oneHourTraffic;
        this.twentyFourHoursTraffic = data.twentyFourHoursTraffic;
        // 데이터 로딩 상태 업데이트
        this.loading = false;
      } catch (error) {
        console.error('트래픽 데이터를 가져오는 동안 오류가 발생했습니다:', error);
        this.loading = false;
      }
    }
  }
}
</script>

